export default `.overlay
.modal
    h1.modal__header #{title}
    form.modal__form
        .modal__actions
            if file
                input.modal__input(type="file" name="avatar" value="   ")
                label.modal__label(for="name")    
            else
                span.modal__file-name ivan.jpeg
        button.modal__button(type=buttonType) #{buttonText}
        if error
            span.modal__error   `;
